.footer {
  padding-top: 120px;
  /*Footer Links styling*/ }
  @media screen and (max-width: 1000px) {
    .footer {
      width: 100%; }
      .footer .margin-250 {
        margin-top: 0px !important; }
      .footer .col-12 {
