        padding: 0 0 0 10px !important; } }
  @media screen and (max-width: 1000px) {
    .footer .dropdown {
      font-size: 16px;
      color: #99bbc1;
      font-weight: bold; } }
  .footer .dropdown .mobile-footer-header {
    padding-top: 8px !important; }
  @media screen and (max-width: 1000px) {
